define("sparta/helpers/or", ["exports"], function (exports) {
  exports["default"] = Ember.Helper.helper(function (params) {
    var i, len, param;
    if (!(params != null && params.length >= 2)) {
      return false;
    }
    for (i = 0, len = params.length; i < len; i++) {
      param = params[i];
      if (param) {
        return true;
      }
    }
    return false;
  });
});